.projectStaffingDetail {
  margin-top: 1px; }
  .projectStaffingDetail__title {
    position: relative;
    padding: 5px 0 5px 20px;
    background-color: #e6e6e6;
    cursor: pointer; }
    .projectStaffingDetail__title:hover {
      color: #1779ba; }
  .projectStaffingDetail__visibilityIndicator {
    display: inline-block;
    min-width: 20px;
    pointer-events: none; }
  .projectStaffingDetail__details {
    display: none; }
    .projectStaffingDetail__details--visible {
      display: block;
      margin: 10px 0 10px 40px;
      list-style: inside; }
  .projectStaffingDetail__removeStaffingLink {
    margin-left: 1rem;
    font-size: 80%;
    cursor: pointer; }



